			TabCur: 1,
			scrollLeft: 0,
			tab: [{
				name: '进行中',
				value: '1'
			}, {
				name: '已结束',
				value: '2'
			}, {
				name: '全部',
				value: '3'
			}],
			contentText: {
				contentdown: '上拉显示更多',
				contentrefresh: '正在加载...',
				contentnomore: '没有更多数据了'
			},
			newsList: 4,
			loadingText: '加载中...',
