		loadingData(e){			
			if(e.timeStamp-this.lastTime>5000){
				console.log('触底了',e)
				this.lastTime=e.timeStamp
				let _self=this
				setTimeout(function(){
					_self.newsList =8;
				},2000)
			}else{
				console.log('重复 了',e)
				return
			}
			
		},
		toadd(){
			uni.navigateTo({
				url: 'addBatch'
			})
		},
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
		}
	}
